<template>
  <div class="stock-list">
    <el-container>
      <el-header>
        <h1>
          <span>No. </span>
          <span>{{ validateForm.orderNumber }}</span>
        </h1>
      </el-header>
      <el-main>
        <!-- form表单 start -->
        <el-form
          :model="validateForm"
          ref="validateForm"
          label-width="100px"
          class="demo-ruleForm"
          :rules="validateFormRules"
        >
          <!-- 单号 start -->
          <!-- <el-form-item label="单号" prop="orderNumber">
            <el-input style="width: 220px" v-model="validateForm.orderNumber" placeholder="请输入单号"></el-input>
          </el-form-item> -->
          <!-- 单号 end -->

          <div class="one-row">
            <!-- 收货单位 start -->
            <el-form-item label="收货单位" prop="selectReceivingIdList">
              <el-cascader
                v-model="validateForm.selectReceivingIdList"
                :options="spotList"
                placeholder="选择收货单位"
                clearable
                :props="{
                  value: 'id',
                  label: 'label',
                  children: 'addressList'
                }"
                @change="onReceivingIdChange"
              />
              <!-- <el-select
                v-model="validateForm.receivingId"
                filterable
                placeholder="选择收货单位"
                @change="onChangeReceiving"
              >
                <el-option
                  v-for="(item, index) in spotList"
                  :key="index"
                  :label="item.name"
                  :value="item.id"
                >
                </el-option>
              </el-select> -->
            </el-form-item>
            <!-- 收货单位 start -->

            <!-- 日期 start -->
            <el-form-item label="日期" prop="orderTime">
              <el-date-picker
                v-model="validateForm.orderTime"
                type="datetime"
                value-format="yyyy-MM-dd hh:mm:ss"
                placeholder="请选择日期"
                :picker-options="pickerOptions"
              >
              </el-date-picker>
            </el-form-item>
            <!-- 日期 end -->
          </div>

          <div class="one-row">
            <!-- 供货单位 start -->
            <el-form-item label="供货单位" prop="selectSpotIdList">
              <el-cascader
                v-model="validateForm.selectSpotIdList"
                :options="spotList"
                placeholder="选择收货单位"
                clearable
                :props="{
                  value: 'id',
                  label: 'label',
                  children: 'addressList'
                }"
                @change="onSpotIdChange"
              />
              <!-- <el-select
                v-model="validateForm.spotId"
                filterable
                placeholder="选择供货单位"
                @change="onChangeSpot"
              >
                <el-option
                  v-for="(item, index) in spotList"
                  :key="index"
                  :label="item.name"
                  :value="item.id"
                >
                </el-option>
              </el-select> -->
            </el-form-item>
            <!-- 供货单位 end -->

            <!-- 电话 start -->
            <!-- <el-form-item label="电话" prop="phone">
              <el-input
                style="width: 220px"
                v-model="validateForm.phone"
                placeholder="请输入电话"
              ></el-input>
            </el-form-item> -->
            <!-- 电话 end -->
          </div>

          <!-- 卡片 start -->
          <el-card class="box-card">
            <div slot="header" class="clearfix">
              <span class="fs-18">新增出库单列表</span>
              <!-- 新增 start -->
              <el-tooltip
                class="add-item"
                effect="dark"
                content="新增"
                placement="top"
              >
                <el-button
                  class="icon-size"
                  style="float: right; padding: 3px 0"
                  type="text"
                  icon="el-icon-circle-plus-outline"
                  @click="onAddTable"
                ></el-button>
              </el-tooltip>
              <!-- 新增 end -->
            </div>

            <!-- 表格 start -->
            <el-form-item class="table-form-item" label="" prop="table">
              <el-table
                class="table"
                :data="tableData"
                style="width: 100%"
                border
                v-loading="tableLoading"
                show-summary
                :summary-method="getSummaries"
                :cell-style="summariesStyle"
              >
                <!-- 序号列 start -->
                <el-table-column label="序号" type="index" width="100">
                </el-table-column>
                <!-- 序号列 end -->
                <el-table-column
                  v-for="(value, key) in tableFields"
                  :key="key"
                  :prop="value"
                  :label="key"
                  min-width="130"
                >
                  <template slot-scope="scope">
                    <!-- 非预览下的视图 start -->
                    <template v-if="dialogState !== 'preview'">
                      <template v-if="value == 'inStockReason'">
                        <!-- 选择出库原因 start -->
                        <el-select
                          v-model="scope.row[value]"
                          filterable
                          placeholder=""
                        >
                          <el-option
                            v-for="(item, index) in inStockReasonList"
                            :key="index"
                            :label="item.label"
                            :value="item.value"
                          >
                          </el-option>
                        </el-select>
                        <!-- 选择出库原因 end -->
                      </template>

                      <template v-else-if="value == 'inStockType'">
                        <!-- 选择出库类型 start -->
                        <el-select
                          v-model="scope.row[value]"
                          filterable
                          placeholder=""
                        >
                          <el-option
                            v-for="(item, index) in inStockTypeList"
                            :key="index"
                            :label="item.label"
                            :value="item.value"
                          >
                          </el-option>
                        </el-select>
                        <!-- 选择出库类型 end -->
                      </template>

                      <template v-else-if="value == 'goodsCode'">
                        <!-- 选择配件编码 start -->
                        <el-select
                          v-model="scope.row[value]"
                          filterable
                          placeholder=""
                          @change="onChangeGoods(scope.row)"
                        >
                          <el-option
                            v-for="(item, index) in goodsList"
                            :key="index"
                            :label="item.code"
                            :value="item.code"
                          >
                            <span style="float: left">{{ item.code }}</span>
                            <span
                              style="float: right;
                                color: #a3a6ad;
                                font-size: 13px;
                              "
                              >{{ item.name }}</span
                            >
                          </el-option>
                        </el-select>
                        <!-- 选择配件编码 end -->
                      </template>

                      <!-- 如果是商品名称或规格型号或单位或金额，则禁用输入 -->
                      <template
                        v-else-if="
                          value == 'goodsName' ||
                            value == 'model' ||
                            value == 'unit' ||
                            value == 'totalAmount'
                        "
                      >
                        <el-input
                          class="color-606266"
                          v-model="scope.row[value]"
                          disabled
                        ></el-input>
                      </template>

                      <!-- 如果是数量或单价，则执行 onInput 事件 -->
                      <template
                        v-else-if="
                          value == 'goodsAmount' || value == 'unitPirce'
                        "
                      >
                        <el-input
                          v-model="scope.row[value]"
                          @input="onInput(scope.row)"
                          type="number"
                        ></el-input>
                      </template>

                      <!-- 如果是快递费，则使用数字输入框 -->
                      <template v-else-if="value == 'expressFee'">
                        <el-input
                          v-model="scope.row[value]"
                          type="number"
                        ></el-input>
                      </template>

                      <template v-else-if="value == 'isAnError'">
                        <!-- 选择是否因营运不当造成故障 start -->
                        <el-select
                          v-model="scope.row[value]"
                          filterable
                          placeholder=""
                        >
                          <el-option
                            v-for="(item, index) in isAnErrorList"
                            :key="index"
                            :label="item.label"
                            :value="item.value"
                          >
                          </el-option>
                        </el-select>
                        <!-- 选择是否因营运不当造成故障 end -->
                      </template>

                      <template v-else-if="value == 'warrantyPeriod'">
                        <!-- 选择是否在质保期内 start -->
                        <el-select
                          v-model="scope.row[value]"
                          filterable
                          placeholder=""
                        >
                          <el-option
                            v-for="(item, index) in warrantyPeriodList"
                            :key="index"
                            :label="item.label"
                            :value="item.value"
                          >
                          </el-option>
                        </el-select>
                        <!-- 选择是否因营运不当造成故障 end -->
                      </template>

                      <!-- 否则就是默认输入框（如备注） -->
                      <template v-else>
                        <el-input
                          ref="tableInputRef"
                          v-model="scope.row[value]"
                        ></el-input>
                      </template>
                    </template>
                    <!-- 非预览下的视图 end -->

                    <!-- 预览下的视图 start -->
                    <template v-if="dialogState === 'preview'">
                      {{ tableContentValue(scope.row, value) }}
                    </template>
                    <!-- 预览下的视图 end -->
                  </template>
                </el-table-column>

                <el-table-column label="操作" min-width="130">
                  <template slot-scope="scope">
                    <el-tooltip
                      class="add-item"
                      effect="dark"
                      content="删除"
                      placement="top"
                    >
                      <el-button
                        class="icon-size"
                        @click.native.prevent="
                          delectRow(scope.$index, scope.row)
                        "
                        type="text"
                        icon="el-icon-remove-outline"
                      >
                      </el-button>
                    </el-tooltip>
                  </template>
                </el-table-column>
              </el-table>
            </el-form-item>
            <!-- 表格 end -->
          </el-card>
          <!-- 卡片 end -->

          <div class="one-row mt-10">
            <!-- 送货人 start -->
            <el-form-item label="送货人" prop="deliveryMan">
              <el-input
                v-model="validateForm.deliveryMan"
                placeholder="请输入送货人"
              ></el-input>
            </el-form-item>
            <!-- 送货人 end -->

            <!-- 送货人电话 start -->
            <el-form-item label="送货人电话" prop="phone">
              <el-input
                v-model="validateForm.phone"
                placeholder="请输入送货人电话"
                type="number"
              ></el-input>
            </el-form-item>
            <!-- 送货人电话 end -->

            <!-- 收货人 start -->
            <el-form-item label="收货人" prop="reveiver">
              <el-input
                v-model="validateForm.reveiver"
                placeholder="请输入收货人"
              ></el-input>
            </el-form-item>
            <!-- 收货人 end -->

            <!-- 收货人电话 start -->
            <el-form-item label="收货人电话" prop="receivingPhone">
              <el-input
                v-model="validateForm.receivingPhone"
                placeholder="请输入收货人电话"
                type="number"
              ></el-input>
            </el-form-item>
            <!-- 收货人电话 end -->
          </div>
        </el-form>
        <!-- form 表单 end -->
        <!-- 保存 start -->
        <el-tooltip
          class="mt-10"
          effect="dark"
          content="保存"
          placement="top"
          v-if="dialogState !== 'preview'"
        >
          <el-button
            style="width: 100%"
            type="primary"
            icon="el-icon-check"
            @click="onSaveData('validateForm')"
            >保存</el-button
          >
        </el-tooltip>
        <!-- 保存 end -->

        <!-- 下载 start -->
        <el-tooltip
          class="mt-10"
          effect="dark"
          content="下载"
          placement="top"
          v-if="dialogState === 'preview'"
        >
          <el-button
            style="width: 100%"
            type="primary"
            icon="el-icon-download"
            :loading="downloadLoading"
            @click="onDownloadOrder"
            >下载</el-button
          >
        </el-tooltip>

        <!-- 隐藏的a标签，用来导出excel -->
        <a id="exportOrder" class="exportOrder" :href="downloadUrl" download>
        </a>
        <!-- 下载 end -->
      </el-main>
      <!-- <el-footer>Footer</el-footer> -->
    </el-container>
  </div>
</template>

<script>
import { transform, createUrl } from '../utils/utils.js'
export default {
  name: '',
  props: {
    // 配件信息列表
    goodsList: {
      type: Array,
      required: true,
      default: []
    },
    // 仓库信息列表
    spotList: {
      type: Array,
      required: true,
      default: []
    },
    // 日期快捷选项配置
    pickerOptions: {
      type: Object,
      required: true,
      default: {}
    },
    // 当前是新增dialog还是编辑dialog
    dialogState: {
      type: String,
      required: true,
      default: ''
    },
    // 当前编辑行的单号
    currentRowOrderNumber: {
      type: String,
      required: true,
      default: ''
    },
    // 出库原因列表
    inStockReasonList: {
      type: Array,
      required: true,
      default: []
    },
    // 出库原因类型
    inStockTypeList: {
      type: Array,
      require: true,
      default: []
    },
    // 因营运不当造成故障选项
    isAnErrorList: {
      type: Array,
      require: true,
      default: []
    }
  },
  data() {
    /**
     * 公共验证函数(验证数字值)
     * @params message - 提示信息
     */
    function checkNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          // 如果值为空，可以回调
          callback()
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填数字值)
     * @params message - 提示信息
     */
    function checkRequireNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填电话)
     * @params message - 提示信息
     */
    function checkRequirePhoneMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        }

        var reg = /^1[3456789]\d{9}$/
        if (!reg.test(value)) {
          callback(new Error(name + '必须是正确的手机号'))
        } else {
          callback()
        }
      }
    }

    return {
      // dialogModel: "",
      tableFields: {
        //导出Excel表格的表头设置
        出库原因: 'inStockReason',
        出库类型: 'inStockType',
        商品编码: 'goodsCode',
        商品名称: 'goodsName',
        规格型号: 'model',
        单位: 'unit',
        数量: 'goodsAmount',
        // '单价(元)': 'unitPirce',
        // '金额(元)': 'totalAmount',
        '销售单价/元': 'unitPirce',
        '销售金额/元': 'totalAmount',
        快递单号: 'courierNumber',
        '快递费/元': 'expressFee',
        质保期内: 'warrantyPeriod',
        因营运不当造成故障: 'isAnError',
        备注: 'notes'
      },
      tableData: [
        {
          goodsAmount: '',
          inStockReason: '',
          inStockType: '',
          goodsCode: '',
          goodsId: '',
          goodsName: '',
          model: '',
          notes: '',
          totalAmount: '',
          unit: '',
          unitPirce: ''
        }
      ],

      tableLoading: false,

      downloadLoading: false, // 下载按钮的loading加载
      downloadUrl: '', // 下载的url

      warrantyPeriodList: [
        {
          value: '0',
          label: '不在'
        },
        {
          value: '1',
          label: '在'
        }
      ],

      validateForm: {
        orderNumber: '',
        deliveryMan: '',
        deliveryManPhone: '',
        orderTime: '',
        phone: '', // 供货人电话
        receivingId: '',
        receivingName: '',
        reveiver: '',
        receivingPhone: '', // 发货人电话
        spotId: '',
        spotName: '',
        type: 2, // 1、入库；2、出库

        selectReceivingIdList: null, // 选择的收货单位列表
        selectSpotIdList: null // 选择的供货单位列表
      }, // 验证表单对象（一定要在validateForm中定义好初始属性，不然会出现input输入框中的内容无法修改的情况）

      // 提交表单规则
      validateFormRules: {
        // 单号
        orderNumber: [
          {
            validator: this.checkRequireMethod('单号'),
            required: true,
            trigger: ['blur', 'change']
          }
        ],

        // 收货单位
        selectReceivingIdList: [
          {
            validator: this.checkRequireMethod('收货单位'),
            required: true,
            trigger: ['blur', 'change']
          }
        ],

        // 日期
        orderTime: [
          {
            validator: this.checkRequireMethod('日期'),
            required: true,
            trigger: ['blur', 'change']
          }
        ],

        // 供货单位
        selectSpotIdList: [
          {
            validator: this.checkRequireMethod('供货单位'),
            required: true,
            trigger: ['blur', 'change']
          }
        ],

        // 表格验证
        table: [
          {
            validator: this.checkRequireMethod('表格'),
            required: true,
            trigger: ['blur', 'change']
          }
        ],

        // 送货人
        deliveryMan: [
          {
            validator: this.checkRequireMethod('送货人'),
            required: true,
            trigger: ['blur', 'change']
          }
        ],

        // 送货人电话
        phone: [
          {
            validator: checkRequirePhoneMethod('送货人电话'),
            required: true,
            trigger: ['blur', 'change']
          }
        ],

        // 收货人
        reveiver: [
          {
            validator: this.checkRequireMethod('收货人'),
            required: true,
            trigger: ['blur', 'change']
          }
        ],

        // 收货人电话
        receivingPhone: [
          {
            validator: checkRequirePhoneMethod('收货人电话'),
            required: true,
            trigger: ['blur', 'change']
          }
        ]
      }
    }
  },
  created() {
    // console.log(this.goodsList);
    // console.log(this.spotList);
  },
  mounted() {},
  updated() {},
  methods: {
    /**
     * 公共验证函数(验证必填值)
     * @params message - 提示信息
     */
    checkRequireMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        // console.log(this.resolventList);

        // 表格是否为空(true不为空，false为空)
        var tag = false

        // 遍历表格列表，如果其中某项的title没有值时，则校验不能通过
        // console.log(this.tableData);
        this.tableData.forEach((item) => {
          // console.log(item);
          if (name == '表格' && item) {
            if (!item.inStockReason) {
              tag = false // 表示有空值
              return callback(new Error('出库原因不能为空'))
            } else if (!item.inStockType) {
              tag = false // 表示有空值
              return callback(new Error('出库类型不能为空'))
            } else if (!item.goodsCode) {
              tag = false // 表示有空值
              return callback(new Error('商品编码不能为空'))
            } else if (!item.goodsAmount) {
              tag = false // 表示有空值
              return callback(new Error('数量不能为空'))
            } else if (!item.unitPirce) {
              tag = false // 表示有空值
              return callback(new Error('单价不能为空'))
            } else if (!item.totalAmount) {
              tag = false // 表示有空值
              return callback(new Error('金额不能为空'))
            } else {
              tag = true
            }
          }
        })
        // console.log(tag);
        if (name == '表格' && tag) {
          value = '这里随便给一个值就行' // 随便给一个值使验证通过
        }
        // console.log(value);
        // console.log(name);

        if (!value) {
          return callback(new Error(name + '不能为空'))
        } else {
          callback()
        }
      }
    },

    /**
     * 收货单位选项变化时触发
     * 函数说明：通过收货单位id带出收货单位名称、收货人
     */
    onChangeReceiving(receivingId) {
      var receivingName = '' // 收货单位
      var reveiver = '' // 收货人
      this.spotList.forEach((item) => {
        if (item.id == receivingId) {
          receivingName = item.name
          reveiver = item.contacts
        }
      })
      this.validateForm.receivingName = receivingName
      this.validateForm.reveiver = reveiver
    },

    /**
     * 供货单位选项变化时触发
     * 函数说明：通过供货单位id带出供货单位名称、电话、送货人
     */
    onChangeSpot(spotId) {
      var spotName = '' // 供货单位
      var phone = '' // 电话
      var deliveryMan = '' // 发货人
      this.spotList.forEach((item) => {
        if (item.id == spotId) {
          spotName = item.name
          deliveryMan = item.contacts
          phone = item.phone
        }
      })
      this.validateForm.spotName = spotName
      this.validateForm.phone = phone
      this.validateForm.deliveryMan = deliveryMan
    },

    /**
     * 商品编码选项变化时触发
     * 函数说明：通过商品id带出商品名称、规格型号、单位、单价
     */
    onChangeGoods(row) {
      // console.log(row);

      var goodsCode = row.goodsCode // 商品编码

      this.goodsList.forEach((item) => {
        if (item.code == goodsCode) {
          row.goodsId = item.id
          row.goodsName = item.name
          row.model = item.model
          row.unit = item.unit
          row.unitPirce = item.inPrice

          // 计算金额（数量*单价）
          row.totalAmount = row.goodsAmount * row.unitPirce
        }
      })

      this.validateForm.details = this.tableData
    },

    /**
     * 获取单号
     */
    async getOrderNumber() {
      const { data: res } = await this.$http.get(
        '/system/common/getOrderNumber',
        {
          params: {
            type: 2
          }
        }
      )
      // console.log(res);
      this.validateForm.orderNumber = res
    },

    /**
     * 根据单号查询数据(预览)
     */
    async getByOrderNumber() {
      if (!this.validateForm.orderNumber) {
        this.validateForm.orderNumber = null
      }

      const { data: res } = await this.$http.get(
        '/system/inventory_detail/getByOrderNumber',
        {
          params: {
            orderNumber: this.validateForm.orderNumber,
            type: 2 // 1、入库；2、出库
          }
        }
      )
      // console.log(res);
      var validateForm = {
        orderNumber: res.data[0].orderNumber,
        deliveryMan: res.data[0].deliveryMan,
        orderTime: res.data[0].orderTime,
        phone: res.data[0].phone,
        receivingId: res.data[0].receivingId,
        receivingName: res.data[0].receivingName,
        reveiver: res.data[0].receiver,
        receivingPhone: res.data[0].receivingPhone,
        spotId: res.data[0].spotId,
        spotName: res.data[0].spotName,
        type: res.data[0].type, // 1、入库；2、出库

        // 初始化选择的收货单位
        selectReceivingIdList:
          data[0].receivingLevelId &&
          data[0].receivingLevelId.split(',') &&
          data[0].receivingLevelId.split(',').map((item) => Number(item)),
        // 初始化选择的供货单位
        selectSpotIdList:
          data[0].spotLevelId &&
          data[0].spotLevelId.split(',') &&
          data[0].spotLevelId.split(',').map((item) => Number(item))
      }

      var tableData = res.data.map((item) => {
        return {
          id: item.id,
          inStockReason: item.inStockReason,
          inStockType: item.inStockType,
          goodsAmount: item.goodsAmount,
          goodsCode: item.goodsCode,
          goodsId: item.goodsId,
          goodsName: item.goodsName,
          model: item.model,
          notes: item.notes,
          totalAmount: item.totalAmount,
          unit: item.unit,
          unitPirce: item.unitPirce,
          courierNumber: item.courierNumber,
          expressFee: item.expressFee,
          warrantyPeriod: item.warrantyPeriod,
          isAnError: item.isAnError
        }
      })

      this.tableData = tableData
      validateForm.details = tableData
      this.validateForm = validateForm
    },

    /**
     * 添加表格数据接口
     */
    async addTableData() {
      const { data: res } = await this.$http.post(
        '/system/inventory_detail/add',
        this.validateForm
      )
      if (res.state == '200') {
        this.$message({
          type: 'success',
          message: '保存成功！'
        })
        this.resetFields()
        this.$emit('save')
      } else {
        this.$message({
          type: 'error',
          message: res.msg
        })
      }
    },

    /**
     * 编辑表格数据接口
     */
    async editTableData() {
      const { data: res } = await this.$http.post(
        '/system/inventory_detail/edit',
        this.validateForm
      )
      if (res.state == '200') {
        this.$message({
          type: 'success',
          message: '编辑成功！'
        })
        this.resetFields()
        this.$emit('save')
      } else {
        this.$message({
          type: 'error',
          message: '编辑失败！'
        })
      }
    },

    /**
     * 导出单据
     */
    async getExportOrderData() {
      this.downloadLoading = true
      const { data: res } = await this.$http.get(
        '/system/inventory_detail/downloadOrder',
        {
          params: {
            orderNumber: this.validateForm.orderNumber,
            type: 2 // 1、入库；2、出库
          },
          responseType: 'arraybuffer' // 这里必须制定响应的数据类型为arraybuffer，否则导出的文件无法打开
        }
      )
      if (res) {
        const url = window.URL.createObjectURL(
          new Blob([res], {
            type:
              'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
          })
        )
        const a = document.createElement('a')
        console.log(url)
        a.href = url
        a.download = '出库单'
        a.click()

        this.$message({
          message: '导出成功',
          type: 'success'
        })
      } else {
        this.$message({
          message: '导出失败',
          type: 'error'
        })
      }
      this.downloadLoading = false

      // var params = {
      //   url: '/system/inventory_detail/downloadOrder',
      //   orderNumber: this.validateForm.orderNumber,
      //   type: 2 // 1、入库；2、出库
      // }

      // var downloadUrl = createUrl(params)

      // this.downloadUrl = downloadUrl
      // this.$nextTick(() => {
      //   var exportOrderEle = document.getElementById('exportOrder')
      //   exportOrderEle.click()
      // })
    },

    // 点击父组件的新增按钮触发
    onAddClick(args) {
      this.getOrderNumber()
    },

    // 点击父组件的编辑按钮触发
    onEditClick(args) {
      this.validateForm.orderNumber = this.currentRowOrderNumber
      this.getByOrderNumber()
    },

    // 点击父组件的预览按钮触发
    onPreviewClick(data) {
      // console.log(data);
      // this.validateForm.orderNumber = orderNumber;

      var validateForm = {
        orderNumber: data[0].orderNumber,
        deliveryMan: data[0].deliveryMan,
        orderTime: data[0].orderTime,
        phone: data[0].phone,
        receivingId: data[0].receivingId,
        receivingName: data[0].receivingName,
        reveiver: data[0].receiver,
        receivingPhone: data[0].receivingPhone,
        spotId: data[0].spotId,
        spotName: data[0].spotName,
        type: data[0].type, // 1、入库；2、出库

        // 初始化选择的收货单位
        selectReceivingIdList:
          data[0].receivingLevelId &&
          data[0].receivingLevelId.split(',') &&
          data[0].receivingLevelId.split(',').map((item) => Number(item)),
        // 初始化选择的供货单位
        selectSpotIdList:
          data[0].spotLevelId &&
          data[0].spotLevelId.split(',') &&
          data[0].spotLevelId.split(',').map((item) => Number(item))
      }

      var tableData = data.map((item) => {
        return {
          id: item.id,
          inStockReason: item.inStockReason,
          inStockType: item.inStockType,
          goodsAmount: item.goodsAmount,
          goodsCode: item.goodsCode,
          goodsId: item.goodsId,
          goodsName: item.goodsName,
          model: item.model,
          notes: item.notes,
          totalAmount: item.totalAmount,
          unit: item.unit,
          unitPirce: item.unitPirce,
          courierNumber: item.courierNumber,
          expressFee: item.expressFee,
          warrantyPeriod: item.warrantyPeriod,
          isAnError: item.isAnError
        }
      })

      this.tableData = tableData
      validateForm.details = tableData
      this.validateForm = validateForm
    },

    /**
     * 自定义合计行函数
     */
    getSummaries(param) {
      const { columns, data } = param
      const sums = []
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = '合计(大写)'
          return
        }
        // console.log(column);

        const values = data.map((item) => Number(item[column.property]))
        if (!values.every((value) => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr)
            if (!isNaN(value)) {
              return prev + curr
            } else {
              return prev
            }
          }, 0)
          // sums[index] += ' 元';
        } else {
        }

        // 如果当前列为出库原因、出库类型、商品编码、单位、数量、销售单价、快递单号、快递费、质保期内、因营运不当造成故障、备注，则显示为空
        if (
          column.property == 'inStockReason' ||
          column.property == 'inStockType' ||
          column.property == 'goodsCode' ||
          column.property == 'unit' ||
          column.property == 'goodsAmount' ||
          column.property == 'unitPirce' ||
          column.property == 'courierNumber' ||
          column.property == 'expressFee' ||
          column.property == 'warrantyPeriod' ||
          column.property == 'isAnError' ||
          column.property == 'notes'
        ) {
          sums[index] = ''
        }

        // 如果当前列为商品名称，则显示为"人民币(大写)零元"
        if (column.property == 'goodsName') {
          sums[index] = '人民币(大写)零元'
        }

        // // 如果当前列为单位，则显示为"块"
        // if (column.property == "unit") {
        //   sums[index] = "块"
        // }

        // 如果当前列为金额，则计算总金额
        if (column.property == 'totalAmount') {
          columns.forEach((item, index2) => {
            // 如果当前列为规格型号，则显示大写零钱
            if (item.property == 'model') {
              sums[index2] = transform(String(sums[index]))
            }
          })

          // 拼接 '元'
          sums[index] += ' 元'
        }
      })

      return sums
    },

    /**
     * 重置表单函数
     */
    resetFields() {
      this.tableData = [
        {
          inStockReason: '',
          inStockType: '',
          goodsAmount: '',
          goodsCode: '',
          goodsId: null,
          goodsName: '',
          model: '',
          notes: '',
          totalAmount: '',
          unit: '',
          unitPirce: ''
        }
      ] // 重置表格数据
      this.dialogVisible = false
      this.$refs.validateForm.resetFields() // 重置表单
    },

    /**
     * 当输入数量或单价时触发
     */
    onInput(row) {
      // console.log(row);
      row.totalAmount = row.goodsAmount * row.unitPirce
    },

    // 点击删除表格行时触发
    delectRow(index, row) {
      this.tableData.splice(index, 1)
      // 获取焦点再失去焦点，实现使校验提示消失的效果
      // this.$refs.tableInputRef[0]表示找到第一个input输入框
      // console.log(this.$refs.tableInputRef);
      this.$refs.tableInputRef[0].focus()
      this.$refs.tableInputRef[0].blur()
    },

    // 点击新增表格数据时触发
    onAddTable() {
      this.tableData.push({
        inStockReason: '',
        inStockType: '',
        goodsAmount: '',
        goodsCode: '',
        goodsId: null,
        goodsName: '',
        model: '',
        notes: '',
        totalAmount: '',
        unit: '',
        unitPirce: ''
      }) // 同样的，一定要在对象中定义好初始属性，不然会出现input输入框中的内容无法修改的情况
    },

    // 点击保存表格数据时触发（提交）
    onSaveData(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (this.dialogState == 'add') {
            // 添加
            this.addTableData()
          } else if (this.dialogState == 'edit') {
            // 编辑
            this.editTableData()
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // 点击下载时触发
    async onDownloadOrder() {
      // this.resetFields()
      // this.$emit("download")

      this.$confirm('确认导出Excel表格?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          this.getExportOrderData()
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消导出Excel表格'
          })
        })
    },

    /**
     * 设置单元格样式
     */
    summariesStyle({ row, column, rowIndex, columnIndex }) {
      // console.log(row, column, rowIndex, columnIndex);
      // if (column.property == "model") {
      //   return "background: green;color:white"
      // }
    },

    // 收货单位选项变化时触发
    onReceivingIdChange(value) {
		var that = this
		this.spotList.forEach(item => {
			if(item.id == value[0]){
				item.addressList.forEach(ele => {
					if(ele.id == value[1]){
						ele.addressList.forEach(val => {
							if(val.id == value[2]){
								val.addressList.forEach(nam => {
									that.validateForm.reveiver = nam.name
									that.validateForm.receivingPhone = nam.phone
								})
							}
						})
					}
				})
			}
		})
      // 选择的收货单位
      this.validateForm.receivingId = value && value[value.length - 1]
    },

    // 供货单位选项变化时触发
    onSpotIdChange(value) {
		var that = this
		this.spotList.forEach(item => {
			if(item.id == value[0]){
				item.addressList.forEach(ele => {
					if(ele.id == value[1]){
						ele.addressList.forEach(val => {
							if(val.id == value[2]){
								val.addressList.forEach(nam => {
									that.validateForm.deliveryMan = nam.name
									that.validateForm.phone = nam.phone
								})
							}
						})
					}
				})
			}
		})
      // 选择的供货单位
      this.validateForm.spotId = value && value[value.length - 1]
    }
  },
  computed: {
    dialogModel: {
      get() {
        return this.dialogState
      },
      set(newValue) {
        // console.log(newValue);
      }
    },

    tableContentValue() {
      return (row, value) => {
        // console.log(row, value);
        var result = ''
        if (value == 'inStockReason') {
          if (row[value] == 1) {
            result = '生产'
          } else if (row[value] == 2) {
            result = '配件申请'
          } else if (row[value] == 3) {
            result = '故障上报'
          } else if (row[value] == 4) {
            result = '损坏上报'
          }
        } else if (value == 'inStockType') {
          if (row[value] == 1) {
            result = '生产'
          } else if (row[value] == 2) {
            result = '销售'
          } else if (row[value] == 3) {
            result = '售后'
          }
        } else {
          result = row[value]
        }
        return result
      }
    }
  },
  watch: {}
}
</script>

