<template>
  <div class="partner-company">
    <el-container>
      <el-header class="header">
        <!-- 搜索表单 start -->
        <el-form
          :inline="true"
          :model="searchValidateForm"
          ref="searchValidateForm"
          label-width="100px"
          class="search-validate-form"
          :rules="searchValidateFormRules"
        >
          <!-- 请选择日期统计方式 start -->
          <!-- <el-form-item label="" prop="">
            <el-select v-model="selectDateState" filterable placeholder="请选择日期统计方式" @change="onDateChange">
              <el-option v-for="(item, index) in dateStateOptions" :key="index" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </el-form-item> -->
          <!-- 请选择日期统计方式 end -->

          <!-- 输入起止时间 start -->
          <!-- <el-form-item label="" prop="startEndDate">
            <el-date-picker v-model="searchValidateForm.startEndDate" unlink-panels :type="dateType" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期" :value-format="valueFormat">
            </el-date-picker>
          </el-form-item> -->
          <el-form-item label="" prop="searchStartEndDate">
            <el-date-picker
              v-model="searchStartEndDate"
              unlink-panels
              type="daterange"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
              :value-format="valueFormat"
            >
            </el-date-picker>
          </el-form-item>
          <!-- 输入起止时间 end -->

          <!-- 请输入单号 start -->
          <el-form-item label="" prop="orderNumber">
            <el-input
              v-model="searchValidateForm.orderNumber"
              placeholder="请输入单号"
            >
            </el-input>
          </el-form-item>
          <!-- 请输入单号 end -->

          <!-- 请选择出库原因 start -->
          <el-form-item label="" prop="inStockReason">
            <el-select
              v-model="searchValidateForm.outStockReason"
              filterable
              placeholder="请选择出库原因"
              clearable
            >
              <el-option
                v-for="(item, index) in searchInStockReasonOptions"
                :key="index"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请选择出库原因 end -->

          <!-- 请选择出库类型 start -->
          <el-form-item label="" prop="inStockType">
            <el-select
              v-model="searchValidateForm.outStockType"
              filterable
              placeholder="请选择出库类型"
              clearable
            >
              <el-option
                v-for="(item, index) in searchInStockTypeOptions"
                :key="index"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请选择出库类型 end -->

          <!-- 请选择发货单位start -->
          <el-form-item label="" prop="spotId">
            <el-select
              v-model="searchValidateForm.spotId"
              filterable
              placeholder="请选择发货单位"
              clearable
            >
              <el-option
                v-for="(item, index) in searchSpotNameOptions"
                :key="index"
                :label="item.name"
                :value="item.id"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请选择发货单位 end -->

          <!-- 请选择配件名称 start -->
          <el-form-item label="" prop="goodId">
            <el-select
              v-model="searchValidateForm.goodId"
              filterable
              placeholder="请选择配件名称"
              clearable
            >
              <el-option
                v-for="(item, index) in searchGoodsNameOptions"
                :key="index"
                :label="item.name"
                :value="item.id"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请选择配件名称 end -->

          <!-- 请选择签收状态 start -->
          <el-form-item label="" prop="signInState">
            <el-select
              v-model="searchValidateForm.signInState"
              filterable
              placeholder="请选择签收状态"
              clearable
            >
              <el-option
                v-for="(item, index) in signInStateOptions"
                :key="index"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请选择签收状态 end -->
        </el-form>
        <!-- 搜索表单 end -->

        <!-- 查询数据 start -->
        <el-button
          type="primary"
          icon="el-icon-search"
          @click="onSearch('searchValidateForm')"
          >搜索</el-button
        >
        <!-- 查询数据 end -->

        <!-- 重置 start -->
        <el-button type="primary" icon="el-icon-refresh-right" @click="onReset"
          >重置</el-button
        >
        <!-- 重置 end -->

        <!-- 新增 start -->
        <el-button
          type="primary"
          icon="el-icon-circle-plus-outline"
          @click="onAdd"
          >新增</el-button
        >
        <!-- 新增 end -->

        <!-- 预览 start -->
        <el-popover placement="bottom" title="请输入单号" trigger="click">
          <el-button
            slot="reference"
            class="preview-btn"
            type="primary"
            icon="el-icon-view"
            >预览</el-button
          >

          <div class="flex-center">
            <el-form
              :inline="true"
              :model="previewValidateForm"
              ref="previewValidateForm"
              label-width="100px"
              class="search-validate-form"
              :rules="previewValidateFormRules"
            >
              <el-form-item label="" prop="previewOrderNumber">
                <el-input
                  v-model="previewValidateForm.previewOrderNumber"
                  placeholder="请输入单号"
                ></el-input>
              </el-form-item>

              <el-button
                type="primary"
                @click="onPreview('previewValidateForm')"
                >确定</el-button
              >
            </el-form>
          </div>
        </el-popover>

        <!-- 预览 end -->

        <!-- 导出 Excel start -->
        <el-button
          type="primary"
          :loading="downloadLoading"
          icon="el-icon-download"
          @click="exportExcel"
          >导出Excel</el-button
        >

        <!-- 隐藏的a标签，用来导出excel -->
        <a id="exportA" :href="downloadUrl" download> </a>
        <!-- 导出 Excel end -->

        <!-- 导入EXCEL start -->
        <el-upload
          ref="uploadImport"
          action="https://run.mocky.io/v3/9d059bf9-4660-45f2-925d-ce80ad6c4d15"
          :limit="1"
          :on-exceed="handleImportExceed"
          :on-change="handleImportChange"
          :show-file-list="false"
          accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
          :auto-upload="false"
          mr-3
          flex
          justify-end
        >
          <el-button
            type="primary"
            :loading="importLoading"
            icon="el-icon-upload2"
            >导入EXCEL</el-button
          >
          <!-- <template #tip>
            <div class="el-upload__tip">
              jpg/png files with a size less than 2M.
            </div>
          </template> -->
        </el-upload>
        <!-- 导入EXCEL end -->
      </el-header>

      <el-main>
        <!-- 表格 start -->
        <el-table
          class="table"
          :data="tableData"
          style="width: 100%"
          border
          v-loading="tableLoading"
          :header-cell-style="{
            background: '#f2f2f2',
            color: '#606266',
            height: '38px',
            padding: '0px'
          }"
          show-summary
          :summary-method="getSummaries"
          :cell-style="{ height: '38px', padding: '0' }"
        >
          <el-table-column
            v-for="(value, key) in tableFields"
            :key="key"
            :prop="value"
            :label="key"
            min-width="130"
          >
            <template slot-scope="scope">
              <div>
                {{ tableContentValue(scope.row, value) }}
              </div>
            </template>
          </el-table-column>
          <el-table-column label="操作" min-width="230">
            <template slot-scope="scope">
              <!-- <el-button @click.native.prevent="editRow(scope.$index, scope.row)" type="text" size="small" :disabled="scope.row.status !== 0">
                编辑
              </el-button> -->
              <el-button
                @click.native.prevent="confirmRow(scope.$index, scope.row)"
                type="text"
                size="small"
                :disabled="scope.row.status !== 0"
              >
                确认
              </el-button>
              <el-button
                @click.native.prevent="revokeRow(scope.$index, scope.row)"
                type="text"
                size="small"
                :disabled="scope.row.status !== 0"
              >
                撤销
              </el-button>

              <el-button
                :disabled="
                  scope.row.signInState == 0 || scope.row.signInState == 1
                "
                @click.native.prevent="unsigned(scope.$index, scope.row)"
                type="danger"
                size="mini"
              >
                不签收
              </el-button>
              <el-button
                :disabled="
                  scope.row.signInState == 0 || scope.row.signInState == 1
                "
                @click.native.prevent="signIn(scope.$index, scope.row)"
                type="primary"
                size="mini"
              >
                签收
              </el-button>
            </template>
          </el-table-column>
        </el-table>
        <!-- 表格 end -->

        <!-- 导出的表格 start -->
        <!-- 此表格始终保持隐藏 -->
        <el-table
          id="outTable"
          class="table"
          :data="exportTableData"
          style="width: 100%; display: none"
          border
        >
          <el-table-column
            v-for="(value, key) in tableFields"
            :key="key"
            :prop="value"
            :label="key"
            min-width="130"
            align="center"
          >
          </el-table-column>
        </el-table>
        <!-- 导出的表格 end -->
      </el-main>

      <el-footer>
        <!-- 分页 start -->
        <div class="block">
          <el-pagination
            background
            layout="prev, pager, next, total, sizes, jumper"
            :total="total"
            :current-page="currentPage"
            :page-sizes="[10, 20, 30, 40]"
            :page-size="pageSize"
            @current-change="onCurrentChange"
            @size-change="onSizeChange"
          >
          </el-pagination>
        </div>
        <!-- 分页 end -->
      </el-footer>

      <!-- dialog 弹窗 start -->
      <el-dialog
        title="出库单"
        :close-on-click-modal="false"
        :fullscreen="true"
        :visible.sync="dialogVisible"
        :before-close="handleClose"
      >
        <stock-list
          ref="stockList"
          :goodsList="searchGoodsNameOptions"
          :spotList="spotIdOptions"
          :pickerOptions="pickerOptions"
          :dialogState="dialogState"
          :currentRowOrderNumber="currentRowOrderNumber"
          :inStockReasonList="searchInStockReasonOptions"
          :inStockTypeList="searchInStockTypeOptions"
          :isAnErrorList="isAnErrorOptions"
          @save="onSave"
        ></stock-list>
      </el-dialog>
      <!-- dialog 弹窗 end -->
    </el-container>
  </div>
</template>

<script>
// import JsonExcel from 'vue-json-excel'
import FileSaver from 'file-saver'
import XLSX from 'xlsx'

import { createUrl } from '../utils/utils.js'
import StockList from './StockList.vue'
export default {
  name: 'SettlementFlow',
  data() {
    /**
     * 公共验证函数(验证数字值)
     * @params message - 提示信息
     */
    function checkNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          // 如果值为空，可以回调
          callback()
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填数字值)
     * @params message - 提示信息
     */
    function checkRequireNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填值)
     * @params message - 提示信息
     */
    function checkRequireMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        } else {
          callback()
        }
      }
    }

    return {
      showTable: true, // 是否显示表格
      feeInput: '', // 请输入手续费
      taxInput: '', // 请输入税点
      tableFields: {
        //导出Excel表格的表头设置
        // 出库日期: 'orderTime',
		入库日期: 'createTime',
		故障单号:'errorRecordsNo',
        单号: 'orderNumber',
        收货单位: 'receivingName',
        配件编码: 'goodsCode',
        配件名称: 'goodsName',
        规格型号: 'model',
        数量: 'goodsAmount',
        单位: 'unit',
        备注: 'notes',
        发货单位: 'spotName',
        // 电话: 'phone',
        送货人: 'deliveryMan',
        送货人电话: 'phone',
        收货人: 'receiver',
        收货人电话: 'receivingPhone',
        // '进货单价/元': 'unitPirce',
        // '进货金额/元': 'totalAmount',
        '销售单价/元': 'unitPirce',
        '销售金额/元': 'totalAmount',
        快递单号: 'courierNumber',
        '快递费/元': 'expressFee',
        出库类型: 'inStockType',
        出库原因: 'inStockReason',
        质保期内: 'warrantyPeriod',
        因营运不当造成故障: 'isAnError',
        签收状态: 'signInState',
        状态确认人: 'confirmedByName'
      },
      tableData: [], // 表格数据

      tableTotalData: {}, // 表格合计数据

      tableLoading: false,

      searchCompanyOptions: [], // 搜索的公司选项
      searchScenicOptions: [], // 搜索的景区选项
      receivingOptions: [], // 收货景区列表

      searchInStockReasonOptions: [
        {
          value: '1',
          label: '生产'
        },
        {
          value: '2',
          label: '配件申请'
        },
        {
          value: '3',
          label: '故障上报'
        },
        {
          value: '4',
          label: '损坏上报'
        }
      ], // 出库原因选项

      searchInStockTypeOptions: [
        {
          value: '1',
          label: '生产'
        },
        {
          value: '2',
          label: '销售'
        },
        {
          value: '3',
          label: '售后'
        }
      ], // 出库原因选项

      signInStateOptions: [
        {
          value: '0',
          label: '未签收'
        },
        {
          value: '1',
          label: '已签收'
        }
      ], // 签收状态选项

      isAnErrorOptions: [
        {
          value: '0',
          label: '否'
        },
        {
          value: '1',
          label: '是'
        }
      ], // 运营不当状态选项

      dateStateOptions: [
        {
          value: '1',
          label: '按月查询'
        },
        {
          value: '2',
          label: '按日查询'
        },
        {
          value: '3',
          label: '按年查询'
        }
      ], // 日期统计方式选项
      searchSpotNameOptions: [], // 搜索时供货商选项
      searchGoodsNameOptions: [], // 搜索时配件名称选项

      spotIdOptions: [], // 景区选项列表

      // 快捷选项配置
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now()
        },
        shortcuts: [
          {
            text: '今天',
            onClick(picker) {
              picker.$emit('pick', new Date())
            }
          },
          {
            text: '昨天',
            onClick(picker) {
              const date = new Date()
              date.setTime(date.getTime() - 3600 * 1000 * 24)
              picker.$emit('pick', date)
            }
          },
          {
            text: '一周前',
            onClick(picker) {
              const date = new Date()
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7)
              picker.$emit('pick', date)
            }
          }
        ]
      },

      exportTableData: [], // 导出的全部表格数据
      downloadLoading: false, // 导出excel按钮的加载动画
      importLoading: false, // 导入excel按钮的加载动画

      currentPage: 1, // 当前页数,
      pageSize: 10, // 每页显示条数

      selectDateState: '1', // 日期统计方式（月，日，年）
      dateType: 'daterange', // 日期类型（月，日，年）
      valueFormat: 'yyyy-MM-dd', // 日期格式

      searchValidateForm: {
        startEndDate: '', // 起止日期
        orderNumber: '', // 单号
        spotId: '', // 供货商id
        goodId: '', // 配件id
        signInState: '' // 签收状态
      }, // 搜索表单验证对象
      validateForm: {}, // 表单验证对象
      editValidateForm: {}, // 表单验证对象
      previewValidateForm: {
        previewOrderNumber: '' // 预览单号
      }, // 预览表单对象

      searchPartnerCompanyName: '', // 搜索的公司
      searchScenicName: '', // 搜索的景区
      searchSelectDateState: '1', // 搜索的日期统计方式
      searchStartEndDate: [], // 搜索的起止时间
      searchPayState: '', // 搜索的支付状态

      companys: [], // 公司列表
      companyState: '', // 当前请选择公司

      scenics: [], // 景区列表
      scenicState: '', // 当前请选择景区

      downloadUrl: '',

      total: 0,

      operateStatus: 0, // 操作状态（0、初始状态；1、确认；2、撤销）

      dialogVisible: false,

      dialogState: '', // 当前dialog弹窗是新增还是编辑
      currentRowOrderNumber: '', // 当前行单号

      // previewOrderNumber: "", // 预览单号

      // 搜索表单规则
      searchValidateFormRules: {},
      // 预览表单规则
      previewValidateFormRules: {
        // 单号
        previewOrderNumber: [
          {
            validator: checkRequireMethod('单号'),
            required: true,
            trigger: ['blur', 'change']
          }
        ]
      },

      // 提交表单规则
      validateFormRules: {
        // // 机器人数量
        // robotCount: [
        //   { validator: checkNumberMethod('机器人数量'), trigger: ['blur', 'change'] }
        // ],
      },

      // 提交编辑表单规则
      editValidateFormRules: {},

      signState: 0 // 签收状态
    }
  },
  created() {
    // 如果当前环境是开发环境才调用登录方法
    if (process.env.NODE_ENV == 'development') {
      this.login()
    }

    this.getSearchValidateForm()
    this.getValidateForm()
    // this.getCompanyList()
    this.getAllGoodsList()
    this.getAllSpotList()
    this.getFactoryFour()
    this.initTableData()
  },
  beforeMount() {},
  mounted() {},
  methods: {
    // 获取搜索表单验证对象
    getSearchValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = ''
      }
      this.searchValidateForm = {
        ...validateForm,
        goodId: null,
        spotId: null,
        outStockReason: '',
        signInState: null
      }
    },

    // 获取提交表单验证对象
    getValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = ''
      }
      this.validateForm = {
        ...validateForm,
        signInState: null
      }
      // console.log(this.validateForm);
    },

    // 登录(必须)
    async login() {
      const { data: res } = await this.$http.post(
        '/loginSystem?username=lijiazhao&password=lijiazhao'
      )
    },

    /**
     * 初始化表格数据
     * @params - isPage - 是否分页
     */
    async initTableData(isPage = false) {
      var startDate = this.searchStartEndDate && this.searchStartEndDate[0]
      var endDate = this.searchStartEndDate && this.searchStartEndDate[1]

      var params = {}
      if (isPage) {
        params = {
          ...this.searchValidateForm,
          startDate,
          endDate,
          type: 2, // 1、入库；2、出库
          pageNum: (this.currentPage - 1) * this.pageSize, // 页码
          pageSize: this.pageSize // 每页条数
        }
      } else {
        params = {
          ...this.searchValidateForm,
          startDate,
          endDate,
          type: 2, // 1、入库；2、出库
          pageNum: 0, // 页码
          pageSize: this.pageSize // 每页条数
        }
      }

      this.tableLoading = true

      const { data: res } = await this.$http.get(
        '/system/inventory_detail/list',
        {
          params: params
        }
      )
      // console.log(res);
      if (res.state == '200') {
        this.tableData = res.data.list
        this.total = res.data.total

        await this.getTotal()

        this.tableLoading = false
      }
    },

    /**
     * 获取全部配件信息列表（库存管理中的接口）
     */
    async getAllGoodsList() {
      const { data: res } = await this.$http.get('/system/goods_stock/getGoods')
      this.searchGoodsNameOptions = res.data
    },

    /**
     * 获取全部仓库信息列表（库存管理中的接口）
     */
    async getAllSpotList() {
      const { data: res } = await this.$http.get('/system/goods_stock/getSpot')
      this.searchSpotNameOptions = res.data
    },

    /**
     * 获取收货单位选项列表
     */
    async getFactoryFour() {
      const { data: res } = await this.$http.get(
        '/system/address/getFactoryFour'
      )
      // console.log(res);
      if (res.state == '200') {
		  
        // this.spotIdOptions = res.data
		this.spotIdOptions = res.data.filter((item,index,self) => {
			let found = self.findIndex(p => p.label === item.label);
			return found === index
		})
		console.log(this.spotIdOptions)
	
      } else {
        console.log('获取四级联动地址列表失败')
      }
    },

    /**
     * 获取合计数据
     */
    async getTotal() {
