      this.importE(uploadFile.raw)
    }
  },
  components: {
    // DownloadExcel: JsonExcel,
    StockList
  },
  computed: {
    tableContentValue() {
      return (row, value) => {
        // console.log(row, value);
        var result = ''
        if (value == 'type') {
          if (row[value] == 1) {
            result = '待发货'
          } else if (row[value] == 2) {
            result = '已发货'
          } else if (row[value] == 3) {
            result = '已收货'
          } else if (row[value] == 4) {
            result = '已关闭'
          }
        } else if (value == 'outStockReason') {
          if (row[value] == 1) {
            result = '生产'
          } else if (row[value] == 2) {
            result = '配件申请'
          } else if (row[value] == 3) {
            result = '故障上报'
          } else if (row[value] == 4) {
            result = '损坏上报'
          }
        } else if (value == 'inStockType') {
          if (row[value] == 1) {
            result = '生产'
          } else if (row[value] == 2) {
            result = '销售'
          } else if (row[value] == 3) {
            result = '售后'
          } else {
            result = row[value] || '无'
          }
        } else if (value == 'isAnError') {
          if (row[value] == 0) {
            result = '否'
          } else if (row[value] == 1) {
            result = '是'
          } else {
            result = '无'
          }
        } else if (value == 'signInState') {
          if (row[value] == 0) {
            result = '未签收'
          } else if (row[value] == 1) {
            result = '已签收'
          } else if (row[value] == 2) {
            result = '未处理'
          } else {
            result = '未处理'
          }
        } else if (value == 'inStockReason') {
          if (row[value] == 1) {
            result = '生产'
          } else if (row[value] == 2) {
            result = '配件申请'
          } else if (row[value] == 3) {
            result = '故障上报'
          } else if (row[value] == 4) {
            result = '损坏上报'
          }
        } else if (value == 'warrantyPeriod') {
          if (row[value] == '0') {
            result = '不在'
          } else if (row[value] == '1') {
            result = '在'
          }
        } else {
          result = row[value] || '无'
        }
        return result
      }
    }
  }
}
